@use '../sass-utils/' as *;

.hero {
  background: url('../img/educationbg.png');
  background-color: $light-grey;
}

header {
  position: fixed;
  background-color: white;
  width: 100%;
  top: 0;
  padding: 25px;
  z-index: 10;
  @media (min-width: 1025px){
    padding: 20px 15vw;
    img {
      filter: grayscale(0.5);
    }
  }

  nav {
    .sign-up{
      @include lat-tx(24px,600,24px);
      color: $dark-red;
      border: 5px solid $dark-red;
      width: 190px;
      @media (min-width:768px) and (max-width: 1400px ) {
        font-size: 16px;
        width: 150px;
      }
    }

    .shadow-menu {
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      background-color: $dark-red;
      display: flex;
      flex-direction: column;
      padding: 25px 27px;
      visibility: hidden;

      close-wrap {
        opacity: 1;
        i {
          opacity: 1;
        }
      }

      svg {
        width: 50px;
        height: 50px;
      }
      ul {
        opacity: 1;
        padding: 30px 40px;
        gap:45px;
        @media (min-width:768px) {
          gap:5px;
          
          
        }
        li {
          a {
            color: white;
            font-size: 32px;
          }
          .sign-up {
            color: white;
            border:4px solid $light-grey;;
          }
        }
      }
    }
    .showsmenu {
      visibility: visible;
    }
    
   
    svg {
      height: 25px;
      width: 25px;
    }
    ul, a {
      gap:5vw;
      @include lat-tx(25px,600,24px);
      color: #999da5;
      cursor:pointer;
      margin:0;
      text-decoration: none;
      @media (min-width:768px) and (max-width: 1400px )  {
        font-size: 18px;
        gap:2.5vw;
      }
      a:hover{
        color: $dark-red;
      }
    }
  }
}

